.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
  @media screen and (min-width: 480px) {
    width: 480px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
    padding: 0 34px;
  }

  @media screen and (min-width: 1280px) {
    width: 1280px;
    padding: 0 114px;
  }
}
